<template>
  <div class="cms-list-layout">
    <cms-prompt collapse="true">
      <li>显示商城所有用户申请的退/换货申请记录</li>
      <li>退/换货可对单件单个商品进行申请售后操作</li>
      <li>买家申请记录被商家驳回后，将自动进入平台介入步骤，平台可对信息进行审核</li>
    </cms-prompt>

    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>退/换货编号</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.refundNum"></el-input>
      </div>
      <div class="query-group">
        <label>订单号</label>
        <el-input @keyup.native.enter="query" v-model="queryParams.orderNum"></el-input>
      </div>
      <div class="query-group">
        <label>申请时间</label>
        <cms-picker v-model="timer" @change="setTimer" value-format="yyyy-MM-dd HH:mm:ss"></cms-picker>
      </div>
      <div class="query-group">
        <label>退/换货类型</label>
        <el-select @change="query" v-model="queryParams.refundType">
          <el-option label="全部" value=' '></el-option>
          <el-option
            v-for="item in orderStatus"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>退/换货状态</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" :value="''"></el-option>
          <el-option label="待卖家确认" :value="0"></el-option>
          <el-option label="退款中" :value="1"></el-option>
          <el-option label="待买家发货" :value="4"></el-option>
          <el-option label="待卖家收货" :value="5"></el-option>
          <el-option label="待买家收货" :value="6"></el-option>
          <el-option label="待卖家发货" :value="7"></el-option>
          <el-option label="已完成" :value="8"></el-option>
          <el-option label="买家已取消" :value="9"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>平台介入</label>
        <el-select v-model="queryParams.isPlatformIntervent" @change="query">
          <el-option label="全部" value=''></el-option>
          <el-option label="未介入" value='false'></el-option>
          <el-option label="已介入" value='true'></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="exportOrder">
          <i class="iconfont icon-Excel"></i>
          导出当前列表
        </cms-button>
      </div>
    </section>
    <el-tabs v-model="queryParams.refundType" @tab-click="changeOrderStatus">
      <el-tab-pane label="全部" :name="' '"></el-tab-pane>
      <el-tab-pane
        v-for="item in orderStatus"
        :key="item.value"
        :label="item.label"
        :name="item.value"
      ></el-tab-pane>
    </el-tabs>
    <!-- 数据列表 -->
    <table class="order-table" v-loading="loading">
      <tr>
        <th>服务单信息</th>
        <th>数量</th>
        <th style="text-align:left;">退款明细</th>
        <th style="text-align:left;">服务单类型</th>
        <th>服务单状态</th>
        <th>平台介入</th>
        <th>申请时间</th>
        <th>操作</th>
      </tr>
      <tbody v-for="(item,index) in tableData" :key="index">
        <div style="width:100%;height:25px;background:#fff;"></div>
        <!-- 头部 -->
        <tr class="order-header">
          <td colspan="11" style="border:1px solid #EBEEF5;">
            <span style="margin-left:15px">服务单号</span>
            <span>{{item.orderRefund.orderRefundNum}}</span>
            <span style="margin-left:15px">订单编号：</span>
            <span style="margin-right: 50px;">{{item.order.orderCode}}</span>
          </td>
        </tr>
        <tr  style="border:1px solid #EBEEF5;">
          <td class="product-info" style="min-width:300px;max-width:380px;">
            <!-- 商品信息 -->
            <div class="product__inner" style="min-height:80px;">
              <img
                class="product-img"
                :src="$cms.filterUrl(item.productVO.product.coverImage)"
                v-if="item.productVO.product"
              >
              <div class="product-title">
                <h3 class="title-h3" style="margin-top:0;">{{item.productVO.product.name}}</h3>
                <p
                  class="title-p"
                  style="margin-top:10px;"
                >{{showAttr(item.productVO.proSkuAttrList)}}</p>
              </div>
            </div>
          </td>
          <td class="order-count" style="min-width:80px;text-align:center;">x{{item.orderRefund.productRefundCount}}</td>
          <td class="order-count" style="min-width:120px;text-align:left;">
            <div>
              <span>金额:</span>
              <span>￥{{$cms.float(item.orderRefund.refundAmount)}}</span>
            </div>
            <div>
              <span>红包:</span>
              <span>{{item.orderRefund.refundRedPacket}}</span>
            </div>
            <div>
              <span>积分:</span>
              <span>{{item.orderRefund.refundIntegral}}</span>
            </div>
          </td>
          <td class="order-count text-center" style="min-width:80px">
            <span v-if="item.orderRefund.refundType==1">仅退款</span>
            <span v-if="item.orderRefund.refundType==2">退款/退货</span>
            <span v-if="item.orderRefund.refundType==3">换货</span>
          </td>
          <td class="order-count text-center cell" style="min-width:80px"
          >
          <span :class="statusClass(item.orderRefund.status)">
             {{item.orderRefund.status | statusFilter}}
          </span>
         </td>
         <td class="order-count text-center cell" style="min-width:80px"
          >
           <span>{{item.orderRefund.platformIntervent|interventFilter}}</span>
         </td>
          <td
            class="order-count text-center"
            style="min-width:140px"
          >{{$moment(item.orderRefund.applyTime)}}</td>
          <td class="order-operation">
            <div class="mb-10">
              <span class="orderstate__link" v-if="item.orderRefund.refundType==1" v-prem="'/afterSales/refund'"
               @click="$routerLink('/afterSales/refund','view',{id:item.orderRefund.id})">详情
              </span>
              <span class="orderstate__link" v-if="item.orderRefund.refundType==2" v-prem="'/afterSales/fundAndChange'"
               @click="$routerLink('/afterSales/fundAndChange','view',{id:item.orderRefund.id})">详情
              </span>
              <span class="orderstate__link" v-if="item.orderRefund.refundType==3" v-prem="'/afterSales/rechange'"
               @click="$routerLink('/afterSales/rechange','view',{id:item.orderRefund.id})">详情
              </span>
            </div>
            <div class="mb-10" v-if="item.orderRefund.lastIntervent">
              <cms-button
                size="small"
                type="info"
                style="width:100px"
                @click="checkService(item,true)"
                v-if="item.orderRefund.lastIntervent.platformStatus==0">确认</cms-button>
            </div>
            <div class="mb-10" v-if="item.orderRefund.lastIntervent">
              <cms-button
                size="small"
                type="info"
                style="width:100px"
                @click="checkService(item,false)"
               v-if="item.orderRefund.lastIntervent.platformStatus==0">确认不通过</cms-button>
            </div>
            <!-- <div class="mb-10" v-if="item.orderRefund.status==5&&item.orderRefund.platformIntervent">
              <cms-button
                size="small"
                type="info"
                style="width:100px"
                @click="checkService(item,true)"
              >确认收货</cms-button>
            </div> -->
          </td>
        </tr>
      </tbody>
    </table>
    <cms-pagination @change="getPage" :total="total" :size="5"></cms-pagination>
    <!-- 确认操作 -->
    <check-service ref="checkService" @change="changeService"></check-service>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { refundPage, refundExport } from '@/api/order/index.js'
import checkService from './checkService'
var defaultData = {
  page: 1,
  size: 5,
  refundNum: '',
  orderNum: '',
  applyTimeBegin: '',
  applyTimeEnd: '',
  refundType: ' ',
  status: '',
  isPlatformIntervent: ''
}
export default {
  mixins: [list],
  components: {
    checkService
  },
  data() {
    return {
      timer: [],
      activeStatus: '',
      orderStatus: [
        {
          label: '退款单',
          value: '1'
        },
        {
          label: '退货单',
          value: '2'
        },
        {
          label: '换货单',
          value: '3'
        }
      ],
      queryParams: Object.assign({}, defaultData)
    }
  },
  filters: {
    statusFilter(status) {
      var statues = [
        '待卖家确认',
        '退款中',
        '卖家拒绝',
        '',
        '待买家发货',
        '待卖家收货',
        '待买家收货',
        '待卖家发货',
        '已完成',
        '买家取消'
      ] // 状态值对应的属性
      return statues[parseInt(status)]
    },
    interventFilter(val) {
      if (val) {
        return '已介入'
      } else {
        return '未介入'
      }
    }
  },
  methods: {
    exportOrder() { // 导出
      const params = {
        'orderRefundNum': this.queryParams.refundNum,
        'orderCode': this.queryParams.orderNum,
        'applyTimeBegin': this.queryParams.applyTimeBegin,
        'applyTimeEnd': this.queryParams.applyTimeEnd,
        'refundType': this.queryParams.refundType,
        'status': this.queryParams.status,
        'platformIntervent': this.queryParams.isPlatformIntervent
      }
      refundExport(params).then(res => {
        // if (res.code === 200) {
        //   this.$successMessage('导出成功')
        // } else {
        //   this.$errorMessage(res.message)
        // }
        const blob = new Blob([res])
        var a = document.createElement('a')
        a.download = '退换货列表.xls'
        a.href = window.URL.createObjectURL(blob)
        a.click()
      })
    },
    changeService(status) {
      // 操作成功的回调
      if (status) {
        this.query()
      }
    },
    checkService(item, status) {
      // 执行售后操作
      this.$refs.checkService.checkService(item, status)
    },
    changeOrderStatus(value) {
      this.query()
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.applyTimeBegin = timer[0]
      this.queryParams.applyTimeEnd = timer[1]
      this.query()
    },
    showAttr(data) {
      // 转换json字符串属性
      if (data.length <= 0) {
        return ''
      } else {
        let str = ''
        data.forEach(v => {
          const tmp = v.attrName + ':' + v.attrValueName + '  '
          str = str + tmp
        })
        return str
      }
    },
    statusClass(status) {
      var statues = [
        'info',
        'info',
        'error',
        '',
        'info',
        'info',
        'info',
        'info',
        'success',
        'info'
      ] // 状态值对应的属性
      return statues[parseInt(status)]
    }
  },
  created() {
    this.getTableData(refundPage, this.queryParams)
  }
}
</script>

<style scoped lang='scss'>
.product-info {
    width: 33%;
}
.order-count{
  min-width: 80px;
}
</style>




